import {Component, OnInit, ViewChild} from '@angular/core';
import {NgForm} from '@angular/forms';

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css']
})
export class LoginComponent implements OnInit {

  @ViewChild('f') loginForm: NgForm;

  title: string; // see usage as string interpolation
  disabledFlag: boolean; // see usage as property binding
  // inputTxt: string; // see usage as two-way data binding
  username: String;
  password: String;
  errorFlag: boolean;
  errorMsg = 'Invalid username or password !';



  constructor() { }

  ngOnInit() {
    this.title = 'This is Login Page';
    this.disabledFlag = true;
  }

  // binding click event
  buttonClicked(event: any) {
    console.log(event); // your custom code on button click
  }

  login() {

    // fetching data from loginForm
    this.username = this.loginForm.value.username;
    this.password = this.loginForm.value.password;

  }


  }
